@import url('https://fonts.googleapis.com/css2?family=Georama:wght@380&family=Roboto+Condensed:wght@300&display=swap');

@media (max-width: 768px) {
  html, body {
  width: auto !important;
  overflow-x: hidden !important;
  }
}

body {
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,0,0,0.4) 0%, rgba(242,211,50,0.4) 100%);
}

header {
  margin-top: 6vh;
  height: 18vh;
  text-align: center;
}

header span {
  display: inline-block;
  vertical-align: middle;
}

header h2 {
  font-size: 2.2em;
  font-family: 'Georama', sans-serif;
  margin: 0 auto;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 2px rgba(97, 10, 10, 0.28);
}

header h4 {
  margin-top: 10px;
  font-size: 1.4em;
  font-family: 'Roboto Condensed', sans-serif;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 2px rgba(97, 10, 10, 0.28);
}

.PokemonArray {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 60%;
  gap: 20px;
}

.PokemonCard {
  background-color: white;
  border-radius: 8px;
  min-height: 210px;
  flex: 1 1 18%;
  transition: transform .3s;
}

@media (min-width: 1720px) {
  .PokemonArray {
    max-width: 40%;
  }
}

@media (max-width: 1440px) {
  .PokemonArray {
    max-width: 70%;
  }
}

@media (max-width: 1024px) {
  .PokemonArray {
    max-width: 90%;
  }
}
@media (max-width: 425px) {
  

  .PokemonCard {
    flex: 1 1 40%;
  }

  .PokemonArray {
    gap: 15px;
  }
}

@media (max-width: 375px) {
  header {
    margin-bottom: 1vh;
  }

  header h2 {
    font-size: 1.6em;
  }
}

.PokemonCard:hover {
  transform: scale(1.14);
  box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.35);
}

.PokemonCard .header {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.07em;
  color: rgb(88, 88, 88);
  text-indent: 12px;
  margin-top: 10px;
  
}

.PokemonCard .body {
  text-align: center;
}

.PokemonCard img {
  margin-bottom: 5px;
}

.Pagination {
  height: 13vh;
  padding-top: 3vh;
  text-align: center;
}

.Pagination button {
  font-size: 1.2em;
  font-family: 'Roboto Condensed', sans-serif;
  width: 100px;
  height: 50px;
  margin: 10px;
  background-color: white;
  border: 0;
  outline: none;
  border-radius: 10px;
}

.Pagination button:hover {
  box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.35);  
}

.Pagination button:active,
.Pagination button:focus {
  box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.35);  
}

.Spinner {
  margin: 0 auto;
  border: 13px solid rgb(228, 228, 228);
  border-top: 13px solid rgb(167, 167, 167);
  border-radius: 50%;
  width: 100px;
  height: 100px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}